
.icon {
    display: inline-block;
    margin: 6px;
}
.input {
    width: 100%;
    display: inline-block;
    margin: 8px 0;
    padding: 2px;
    background: none;
    border: none;
    color: #013a1e;
    text-transform: uppercase;
    font-size: 1.1rem;
    letter-spacing: 2px;
    &:focus{
        outline: 1px  rgba(177, 205, 193, 0.027) solid;
    }
    &::placeholder {
        color: #adc1b9;
        font-family: "Montserrat", sans-serif;
    }

    &Hints {
        @extend .searchBar;
        margin: 0 !important;
        border-top: none !important;
        border-top-left-radius: 0 !important;
        border-top-right-radius: 0 !important;
        flex-direction: column !important;
        &Item {
            margin: 0.2rem;
            padding:  6px 6px 6px 36px;
            text-transform: uppercase;
            text-align: left;
            &:hover {
                background: rgba(200, 221, 212, 0.75);
                cursor: pointer;
            }
        }
    }
    &None {
        display: none;
    }
}
.searchBar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    width: 340px;
    background: rgba(177, 205, 193, 0.2);
    border: 1px solid #013a1e;
    border-radius: 4px;
    margin: 1rem 1rem 0 1rem;
    @media screen and (max-width: 480px) {
        width: 80%;
    }
    &Hints {
        @extend .searchBar;
        margin: 1rem auto 0;
        border-bottom: none;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
}

.wrapper {
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    justify-content: center;
    align-content: center;
}
